import React, { useEffect } from "react";
import { useState } from "react";
import { Navbar, Feed, Videofeed, Searchfeed, SideBar,SearchPage, PlayTrack,Playsong ,PlayVideo,Genre,Playlist} from "./components";
import Track from "./components/track.jsx";
import { Box, Stack } from "@mui/material";
import { BrowserRouter, Route, Routes } from "react-router-dom";

const App=()=>{
  const [url,seturl] = useState('')
  const [image,setimage] = useState('data:image/png;base64,')
  const [title,settitle] = useState('Play Music')
  const [album,setalbum] = useState('...')
  const [value,setvalue]=useState(true)
 
  const [isPlaying,setIsPlaying] = useState(false)
  const playing=(isPlaying)=>{
    setIsPlaying(isPlaying) 
    console.log('app', isPlaying)
  }
  const turl=(url)=>{
    
    seturl(url);
    console.log(url)
  }
  const imageurl=(image)=>{
    
    setimage(image);
    console.log(image)
  }
  const titlefun=(title)=>{
    
    settitle(title);
    console.log(title)
   
  }
  const albumfun=(album)=>{
    
    setalbum(album);
    console.log(album)
   
  }
  const toggle=(value)=>{
    setvalue(value)
  }
   
  return(
  <BrowserRouter>
    <Navbar toggle={toggle}/>
    <Stack sx={{ flexDirection: "row" }}>
      <Feed display={value}/>
      <Box 
        pd={2}
        sx={{ 
          flexDirection: "row",
          overflowY: "auto",
          height: "100vh",
          flex: 2,
          padding: "20px",
          backgroundColor: "black",
          flex: "100%"
        }} 
      >
        <Routes>
          <Route path="/" exact element={<Searchfeed />} />
          <Route path="/discover" element={<Videofeed />} />
          <Route path="/track/:id" element={<Track turl={turl} playing={playing} imageurl={imageurl} albumfun={albumfun} titlefun={titlefun}/>} />
          <Route path="/playtrack" element={<PlayTrack turl={turl} playing={playing} imageurl={imageurl} albumfun={albumfun} titlefun={titlefun}/>} />
          <Route path="/search" element={<SearchPage />} />
          <Route path="/video" element={<PlayVideo />} />
          <Route path="/trending" element={<Genre/>} />
          <Route path="/playlist" element={<Playlist turl={turl} playing={playing} imageurl={imageurl} albumfun={albumfun} titlefun={titlefun}/>} />
        </Routes>
      </Box>
    </Stack>
    <Playsong audiourl={url} isplaying={isPlaying} imgurl={image} albumname={album} titlename={title}/>
    
  </BrowserRouter>
  )
      
};

export default App;
